.reply-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: 100%;
  overflow-y: auto;

  .empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
    padding: 40px 20px;
    color: #999;

    mat-icon {
      font-size: 48px;
      width: 48px;
      height: 48px;
      color: #ddd;
    }

    p {
      margin: 0;
      font-size: 14px;
    }
  }

  .reply-item {
    padding: 12px;
    background: white;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    transition: all 0.2s;

    &:hover {
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    }

    &.unread {
      background: #f0f7ff;
      border-color: #1976d2;
    }

    .reply-header {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      gap: 12px;
      padding-bottom: 12px;
      margin-bottom: 12px;
      border-bottom: 1px solid #f0f0f0;

      .header-left {
        display: flex;
        align-items: center;
        gap: 10px;
        flex: 1;

        .avatar {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 36px;
          height: 36px;
          background: #1976d2;
          color: white;
          border-radius: 50%;
          font-weight: bold;
          font-size: 14px;
          flex-shrink: 0;
        }

        .sender-info {
          display: flex;
          flex-direction: column;
          gap: 4px;

          strong {
            font-size: 14px;
            color: #333;
          }

          .role-badge {
            display: inline-block;
            padding: 2px 6px;
            border-radius: 3px;
            font-size: 11px;
            font-weight: 600;
            width: fit-content;

            &.role-admin {
              background: #fce4ec;
              color: #c2185b;
            }

            &.role-teacher {
              background: #e8f5e9;
              color: #2e7d32;
            }
          }
        }
      }

      .header-right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        gap: 8px;

        .timestamp {
          font-size: 12px;
          color: #999;
        }

        .status-badge {
          display: flex;
          align-items: center;
          gap: 4px;
          padding: 4px 8px;
          border-radius: 4px;
          font-size: 11px;
          font-weight: 600;

          mat-icon {
            font-size: 14px;
            width: 14px;
            height: 14px;
          }

          &.status-awaiting_response {
            background: #fff3e0;
            color: #e65100;
          }

          &.status-acknowledged {
            background: #e8f5e9;
            color: #2e7d32;
          }

          &.status-resolved {
            background: #c8e6c9;
            color: #1b5e20;
          }
        }
      }
    }

    .reply-content {
      padding: 12px;
      background: #f9f9f9;
      border-radius: 4px;
      font-size: 13px;
      line-height: 1.6;
      color: #666;
      white-space: pre-wrap;
      word-wrap: break-word;
      margin-bottom: 12px;
    }

    .alert-box {
      display: flex;
      gap: 10px;
      padding: 10px 12px;
      background: #fff3e0;
      border-left: 3px solid #ff9800;
      border-radius: 4px;
      margin-bottom: 12px;

      mat-icon {
        color: #ff9800;
        font-size: 18px;
        width: 18px;
        height: 18px;
        flex-shrink: 0;
      }

      .alert-content {
        flex: 1;

        strong {
          display: block;
          font-size: 13px;
          color: #e65100;
          margin-bottom: 4px;
        }

        p {
          margin: 0;
          font-size: 12px;
          color: #cc5500;
        }
      }
    }

    .acknowledged-info {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px 12px;
      background: #e8f5e9;
      border-radius: 4px;
      font-size: 12px;
      color: #2e7d32;
      margin-bottom: 12px;

      mat-icon {
        font-size: 16px;
        width: 16px;
        height: 16px;
      }
    }

    .reply-actions {
      display: flex;
      gap: 4px;
      justify-content: flex-end;

      button {
        &[mat-icon-button] {
          height: 28px;
          width: 28px;
          line-height: 28px;

          &:hover {
            background: #f5f5f5;
          }
        }
      }
    }
  }
}
